function getTeamsNameJsonNew(e) {
 
  return hw.filter((i) => i.ckey == e)[0]?.value;
}

function getTeamsNameJsonShortNew(e) {
    
  return hw.filter((i) => i.ckey == e)[0]?.sn;
}

function getPlayerNameNew(e) {
  if (PN.filter((i) => i.ckey == e).length > 0)
    return PN.filter((i) => i.ckey == e)[0]?.value;
  else return "Player";
}

