function checkBallStatus(status) {
  switch (status) {
    case "B":
      status = "Ball Challu";
      break;
    case "o":
      status = "Over End";
      break;
    case "w":
      status = "Wicket";
      break;
    case "wd":
      status = "Wide Ball";
      break;
    case "wd1":
      status = "Wide + Single";
      break;
    case "wd2":
      status = "Wide + Double";
      break;
    case "wd3":
      status = "Wide + Triple";
      break;
    case "wd4":
      status = "Wide + Four";
      break;
    case "wd5":
      status = "Wide + Five";
      break;
    case "wd6":
      status = "Wide + Six";
      break;
    case "nb":
      status = "No Ball";
      break;
    case "nb1":
      status = "No Ball + Single";
      break;
    case "nb2":
      status = "No Ball + Double";
      break;
    case "nb3":
      status = "No Ball + Triple";
      break;
    case "nb4":
      status = "No Ball + Four";
      break;
    case "nb5":
      status = "No Ball + Five";
      break;
    case "nb6":
      status = "No Ball + six";
      break;
    case "by1":
      status = "Bye Single";
      break;
    case "by2":
      status = "Bye Double";
      break;
    case "by3":
      status = "Bye Triple";
      break;
    case "by4":
      status = "Bye Four";
      break;
    case "lb":
      status = "Leg By";
      break;
    case "lb1":
      status = "Leg By Single";
      break;
    case "lb2":
      status = "Leg By Double";
      break;
    case "lb3":
      status = "Leg By Triple";
      break;
    case "lb4":
      status = "Leg By Four";
      break;
    case "fh":
      status = "Free Hit Bracket";
      break;
    case "cd":
      status = "Catch Drop";
      break;
    case "cc":
      status = "Covers Coming";
      break;
    case "ba":
      status = "Ball In Air";
      break;
    case "no":
      status = "Not Out";
      break;
    case "tu":
    case "u":
      status = "Third Umpire";
      break;
    case "f":
      status = "Faster Aaya";
      break;
    case "s":
      status = "Spinner Aaya";
      break;
    case "e":
      status = "Player Entering";
      break;
    case "a":
      status = "Appeal";
      break;
    case "i":
      status = "Inning Break";
      break;
    case "p":
      status = "Player Injured";
      break;
    case "d":
      status = "Drizzle Startted";
      break;
    case "^1":
      status = "Bowled Out";
      break;
    case "^2":
      status = "Caught Out";
      break;
    case "^3":
      status = "Caught & Bowled";
      break;
    case "^4":
      status = "Run Out";
      break;
    case "^5":
      status = "Lbw Out";
      break;
    case "^6":
      status = "Hit Wicket";
      break;
    case "^8":
      status = "Stump Out";
      break;
    case "^9":
      status = "Mankaded";
      break;
    case "^11":
      status = "Retired Hurt";
      break;
    case "^12":
      status = "Retired Out";
    default:
      status = status;
  }

  return status;
}

function getExchComment(e, r, a, i) {
  switch (e) {
    case "^0":
      e = r + " won the toss and chose to bat";
      break;
    case "^1":
      e = r + " won the toss and chose to bowl";
      break;
    case "^2":
      e = a + " won the toss and chose to bat";
      break;
    case "^3":
      e = a + " won the toss and chose to bowl";
      break;
    case "a":
      e = " Innings break";
      break;
    case "b":
      e = " Drinks break";
      break;
    case "c":
      e = " Lunch Break";
      break;
    case "d":
      e = " Tea Break";
      break;
    case "e":
      e = " Break";
      break;
    case "f":
      e = " Match paused due to rain";
      break;
    case "g":
      e = " Match paused due to low light";
      break;
    case "h":
      e = " Match paused";
      break;
    case "i":
      e = " Match cancelled due to rain";
      break;
    case "j":
      e = " Match cancelled due to low light";
      break;
    case "k":
      e = " Match cancelled";
      break;
    case "l":
      e = " Stumps";
      break;
    case "m":
      e = " Timeout";
      break;
    case "n":
      e = " Match Drawn";
      break;
    case "o":
      e = " Super Over";
      break;
    case "p":
      e = " Match Tied";
      break;
    case "q":
      e = " Abandoned";
      break;
    case "r":
      e = " Rescheduled";
      break;
    case "s":
      e = " Toss delayed";
      break;
    case "t":
      e = " Toss delayed due to rain";
      break;
    case "u":
      e = " Toss delayed due to bad weather";
      break;
    case "v":
      e = " Toss delayed due to low light";
      break;
    case "w":
      e = " Toss delayed due to wet outfield";
      break;
    default:
      e = "Welcome";
  }

  return i.hasOwnProperty("C") ? e + " - " + i.C : e;
}

export function getSocketData(e) {
  let inningsObj = {};
  let teamFlagObj1 = "";
  let teamFlagObj2 = "";
  let parseP1 = {};
  let bWkts = 0;
  let bOvr = 0;
  let bEco = 0;
  let over = [];
  let ovr1 = 0;
  let ovr2 = 0;
  let ovr3 = 0;
  let ovr4 = 0;
  let ovr5 = 0;
  let ovr6 = 0;
  let rrr = "--";
  let crr = "--";
  let ballStatus = "";
  let team1 = "";
  let team2 = "";
  let need = 0;
  let innings = 0;
  let score = "0/0";
  let score1 = "0/0";
  let p1 = "N/A";
  let p2 = "N/A";
  let p1S = 0;
  let p2S = 0;
  let bowlerName = "";
  let bRun = "-";
  let p1Fours = 0;
  let p1Sixes = 0;
  let p1balls = 0;
  let p2balls = 0;
  let p2Fours = 0;
  let p2Sixes = 0;
  let team1short = "";
  let team2short = "";
  let ballName = "";
  let codel = {};
  let i1Over = "";
  let i2Over = "";
  let team1code = "";
  let final_score_obj = {};
  let obj = {};
  let playerArray = [];
  let mainOver0;
  let mainOver;



  parseP1 = JSON.parse(e.response.p1);


  if ("auto" == e.response.type) {
    // Parse 'home' data once and reuse it
    const homeData = JSON.parse(e.response.home || "{}");

    teamFlagObj1 = `https://cricketvectors.akamaized.net/Teams/${
      homeData.b || ""
    }.png`;
    teamFlagObj2 = `https://cricketvectors.akamaized.net/Teams/${
      homeData.c || ""
    }.png`;

    bRun = "-";
    bWkts = "-";
    bOvr = "-";
    bEco = "-";

    // Split over details and handle undefined values
    over = parseP1.A ? parseP1.A.split(".") : [];
    ovr1 = over[0] || "-";
    ovr2 = over[1] || "-";
    ovr3 = over[2] || "-";
    ovr4 = over[3] || "-";
    ovr5 = over[4] || "-";
    ovr6 = over[5] || "-";

    ballStatus = parseP1.B || "";
    ballStatus = checkBallStatus(ballStatus);

    // Get team names and team codes
    team1short =
      homeData.b !== undefined
        ? getTeamsNameJsonShortNew(homeData.b)
        : getTeamsNameJsonShortNew(codel["0"]);
    team1 =
      homeData.b !== undefined
        ? getTeamsNameJsonNew(homeData.b)
        : getTeamsNameJsonNew(codel["0"]);
    team1code = homeData.b !== undefined ? homeData.b : codel["0"];

    team2short =
      homeData.c !== undefined
        ? getTeamsNameJsonShortNew(homeData.c)
        : getTeamsNameJsonShortNew(codel["1"]);
    team2 =
      homeData.c !== undefined
        ? getTeamsNameJsonNew(homeData.c)
        : getTeamsNameJsonNew(codel["1"]);

    // Assign the short names to team variables
    team1 = team1short;
    team2 = team2short;

    final_score_obj.team2 = team2;

    // Get required information from exchange comment


    need = getExchComment(homeData.a, team1, team2, parseP1);

    innings = homeData.d || 0;
    score = "0/0";
    score1 = "0/0";
    p1 = "N/A";
    p2 = "N/A";

    let le = parseP1.j || "0/0";
    let H = parseP1.k || "0/0";

    if (innings % 2 !== 0) {
      le = le.split("(");
      crr = le[0].split("/")[0] / le[1];
      crr = Math.round(100 * crr) / 100;
      score = innings === 1 ? le[0] : `${le[0]} ) & ${homeData.l || ""}`;
      i1Over = le[1];

      H = H.split("(");
      score1 = H[0];
      i2Over = H[1] || "0.0";
    } else if (innings === 2) {
      H = H.split("(");
      le = le.split("(");
      crr = H[0].split("/")[0] / H[1];
      crr = Math.round(100 * crr) / 100;
      score1 = H[0];
      i2Over = H[1] || "0.0";
      score = le[0];
      i1Over = le[1];
    } else {
      H = H.split("(");
      le = le.split("(");
      crr = H[0].split("/")[0] / H[1];
      crr = Math.round(100 * crr) / 100;
      let Jn = homeData.m ? homeData.m.split("(")[0] : "0/0";
      score1 = `${H[0]}  & ${Jn}`;
      i2Over = H[1];
      let Or = homeData.l ? homeData.l.split("(") : ["", ""];
      score = `${le[0]}  & ${Or[0].split("!")[1] || ""}`;
      i1Over = le[1];
    }

    let pe = parseP1.p;

    if (parseP1.b) {
      bowlerName = getPlayerName(parseP1.b[0])[0]?.n || "--";
    }

    let an = parseP1.c;
    if (an) {
      const anData = an.split(".");
      bRun = anData[0];
      bWkts = anData[2];
      bOvr = `${Math.floor(anData[1] / 6)}.${Math.floor(anData[1] % 6)}`;
      bEco = (bRun / (anData[1] / 6)).toFixed(2);
    }

    if (pe) {
      let Jn = parseP1.q || "0.0";
      let Or = parseP1.s || "0.0";
      let ea = parseP1.r || "0.0";
      let qa = parseP1.t || "0.0";
      let Ai = parseP1.b || "N/A";

      p1 = pe.split(".")[0].includes("*")
        ? getPlayerName(pe.split(".")[0].split("*")[0])
        : getPlayerName(pe.split(".")[0]);
      p1 = p1[0]?.n || "--";
      p2 = pe.split(".")[1].includes("*")
        ? getPlayerName(pe.split(".")[1].split("*")[0])
        : getPlayerName(pe.split(".")[1]);
      p2 = p2[0]?.n || "--";

      ballName = getPlayerName(Ai);
      ballName = ballName[0]?.b || "--";

      p1Fours = ea.split(".")[0];
      p1Sixes = ea.split(".")[1];
      p1S = Jn.split(".")[0];
      p1balls = Jn.split(".")[1]?.split("*")[0] || "0";
      p2S = Or.split(".")[0];
      p2balls = Or.split(".")[1]?.split("*")[0] || "0";
      p2Fours = qa.split(".")[0];
      p2Sixes = qa.split(".")[1];
    }
  } else if ("keeda" === e.response.type) {

    need = JSON.parse(e.response.home).info;
    inningsObj = JSON.parse(e.response.home).score_strip;
    team1 = inningsObj[0].short_name;
    team2 = inningsObj[1].short_name;
    score = inningsObj[0].score.split("(")[0];
    i1Over = inningsObj[0].score.split("(")[1].slice(0, -3);
    score1 = inningsObj[1].score.split("(")[0];
    i2Over = inningsObj[1].score.split("(")[1].slice(0, -3);
    ballStatus = "Welcome";
    p1 = JSON.parse(e.response.home).now_batting.b1.name;
    p2 = JSON.parse(e.response.home).now_batting.b2.name;
    p1S =
      JSON.parse(e.response.home).now_batting.b1.stats.runs +
      "(" +
      JSON.parse(e.response.home).now_batting.b1.stats.balls +
      ") *";
    p2S =
      JSON.parse(e.response.home).now_batting.b2.stats.runs +
      "(" +
      JSON.parse(e.response.home).now_batting.b2.stats.balls +
      ")";
    obj = JSON.parse(e.response.home);
    if (obj.has_commentary === 1) {
      if (obj.commentary[0].opta_ball_type === "normal") {
        ballStatus = obj.commentary[0].hasOwnProperty("runs")
          ? obj.commentary[0].runs
          : 0;
      } else if (obj.commentary[1].opta_ball_type === "normal") {
        ballStatus = obj.commentary[1].hasOwnProperty("runs")
          ? obj.commentary[1].runs
          : 0;
      }
      if (obj.commentary[0].opta_ball_type === "normal") {
        ovr1 = obj.commentary[0].runs || "0";
        ovr2 = obj.commentary[1].runs || "0";
        ovr3 = obj.commentary[2].runs || "0";
        ovr4 = obj.commentary[3].runs || "0";
        ovr5 = obj.commentary[4].runs || "0";
        ovr6 = obj.commentary[5].runs || "0";
      } else {
        ovr1 = obj.commentary[1].runs || "0";
        ovr2 = obj.commentary[2].runs || "0";
        ovr3 = obj.commentary[3].runs || "0";
        ovr4 = obj.commentary[4].runs || "0";
        ovr5 = obj.commentary[5].runs || "0";
        ovr6 = obj.commentary[6].runs || "0";
      }
    }
  } else if ("adda" === e.response.type) {
    inningsObj = JSON.parse(e.response.home);
    need = inningsObj.event_sub_status;
    team1 = inningsObj.participants[0].short_name;
    team2 = inningsObj.participants[1].short_name;
    score =
      inningsObj.participants[0].value === ""
        ? "Yet To Bat"
        : inningsObj.participants[0].value.split("(")[0];
    i1Over =
      inningsObj.participants[0].value === ""
        ? "0.0"
        : inningsObj.participants[0].value.split("(")[1].slice(0, -3);
    score1 =
      inningsObj.participants[1].value === ""
        ? "Yet To Bat"
        : inningsObj.participants[1].value.split("(")[0];
    i2Over =
      inningsObj.participants[1].value === ""
        ? "0.0"
        : inningsObj.participants[1].value.split("(")[1].slice(0, -3);
    playerArray = [];
    playerArray =
      inningsObj.participants[0].hasOwnProperty("now") &&
      inningsObj.participants[0].now === "true"
        ? inningsObj.participants[0].players_involved
        : inningsObj.participants[1].players_involved;
    if (playerArray.length !== 0) {
      p1 = playerArray[0]?.name;
      p2 = playerArray[1]?.name;
      p1S = playerArray[0]?.value;
      p2S = playerArray[1]?.value;
    }
    over = inningsObj.event_thisover.split(",");
    if (over.length > 0) {
      ballStatus = over[over.length - 1];
      ovr1 = over[0];
      ovr2 = over[1];
      ovr3 = over[2];
      ovr4 = over[3];
      ovr5 = over[4];
      ovr6 = over[5];
    } else {
      ballStatus = "Welcome";
    }
  } else if ("espncri" === e.response.type) {
    need = "Welcome";
    inningsObj = JSON.parse(e.response.home);
    team1 = inningsObj.match.teams[0].team.abbreviation;
    team2 = inningsObj.match.teams[1].team.abbreviation;
    need = inningsObj.match.statusText;
    ballStatus = inningsObj.recentBallCommentary.ballComments[0]?.totalRuns;
    score =
      inningsObj.match.teams[0].score === "" ||
      inningsObj.match.teams[0].score === null ||
      inningsObj.match.teams[0].score === "null"
        ? "Yet To Bat"
        : inningsObj.match.teams[0].score;
    i1Over =
      inningsObj.match.teams[1].scoreInfo
        .split("/")[1]
        ?.split(",")[0]
        ?.slice(0, -2) || "0.0";
    score1 =
      inningsObj.match.teams[1].score === "" ||
      inningsObj.match.teams[1].score === null ||
      inningsObj.match.teams[1].score === "null"
        ? "Yet To Bat"
        : inningsObj.match.teams[1].score;
    mainOver0 = inningsObj.match.teams[0].scoreInfo;
    if (mainOver0 === null || mainOver0 === "null") {
      score = inningsObj.match.teams[0].score;
      i1Over = inningsObj.match.teams[1].scoreInfo
        .split("/")[1]
        ?.split(",")[0]
        ?.slice(0, -2);
    } else {
      score = inningsObj.match.teams[0].score;
      i1Over = mainOver0;
    }
    mainOver = inningsObj.match.teams[1].scoreInfo;
    if (mainOver === null || mainOver === "null") {
      score1 = inningsObj.match.teams[1].score;
      i2Over = "0.0";
    } else {
      score1 = inningsObj.match.teams[1].score;
      i2Over = mainOver.split("/")[0];
    }
    p1 = inningsObj.livePerformance.batsmen[0].player.longName;
    p2 = inningsObj.livePerformance.batsmen[1].player.longName;
    p1S =
      inningsObj.livePerformance.batsmen[0].runs +
      "(" +
      inningsObj.livePerformance.batsmen[0].balls +
      "*)";
    p2S =
      inningsObj.livePerformance.batsmen[1].runs +
      "(" +
      inningsObj.livePerformance.batsmen[1].balls +
      ")";
    ovr1 = inningsObj.recentBallCommentary.ballComments[0]?.totalRuns || "0";
    ovr2 = inningsObj.recentBallCommentary.ballComments[1]?.totalRuns || "0";
    ovr3 = inningsObj.recentBallCommentary.ballComments[2]?.totalRuns || "0";
    ovr4 = inningsObj.recentBallCommentary.ballComments[3]?.totalRuns || "0";
    ovr5 = inningsObj.recentBallCommentary.ballComments[4]?.totalRuns || "0";
    ovr6 = inningsObj.recentBallCommentary.ballComments[5]?.totalRuns || "0";
  }

  if (!crr) {
    crr = "--";
  }
  return {
    inningsObj,
    teamFlagObj1,
    teamFlagObj2,
    parseP1,
    bWkts,
    bOvr,
    bEco,
    over,
    ovr1,
    ovr2,
    ovr3,
    ovr4,
    ovr5,
    ovr6,
    rrr,
    crr,
    ballStatus,
    team1,
    team2,
    need,
    innings,
    score,
    score1,
    p1,
    p2,
    p1S,
    p2S,
    bowlerName,
    bRun,
    p1Fours,
    p1Sixes,
    p1balls,
    p2balls,
    p2Fours,
    p2Sixes,
    team1short,
    team2short,
    ballName,
    codel,
    i1Over,
    i2Over,
    team1code,
    final_score_obj,
    obj,
    playerArray,
    mainOver0,
    mainOver,
  };
}


